@import url(https://fonts.googleapis.com/css?family=Roboto:300);

.login-page {
    width: 360px;
    padding: 8% 0 0;
    margin: auto;
}
.form {
    position: relative;
    z-index: 1;
    background: #ffffff;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
    font-family: 'Roboto', sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
}

/* 회원가입 아이디 */
.form .register-id {
    font-family: 'Roboto', sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 70%;
    border: 0;
    margin: 14px 0;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
}

.register-id {
    flex: 1; /* 입력 필드가 가용 공간을 차지하도록 설정 */
}

.id-check-container {
    display: flex;
    align-items: center; /* 아이템들을 수직 가운데 정렬 */
    gap: 10px; /* 입력 필드와 버튼 사이의 간격 추가 */
}

.form #button {
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #3b8dbd;
    width: 80%;
    border: 0;
    margin: 0 auto;
    padding: 15px;
    color: #ffffff;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
}
.form #button:hover,
.form #button:active,
.form #button:focus {
    background: #3193c8;
}

.form #duplicate-check {
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #c3c5c5;
    width: 28%;
    border: 0;
    /* margin: 0 auto; */
    padding: 15px;
    color: #ffffff;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
}
.form #duplicate-check:hover,
.form #duplicate-check:active,
.form #duplicate-check:focus {
    background: #e7e3e3;
}

.form .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
}
.form .message a {
    color: #3193c8;
    text-decoration: none;
}
.form .register-form {
    display: none;
}
.container {
    position: relative;
    z-index: 1;
    max-width: 300px;
    margin: 0 auto;
}
.container:before,
.container:after {
    content: '';
    display: block;
    clear: both;
}
.container .info {
    margin: 50px auto;
    text-align: center;
}
.container .info h1 {
    margin: 0 0 15px;
    padding: 0;
    font-size: 36px;
    font-weight: 300;
    color: #1a1a1a;
}
.container .info span {
    color: #4d4d4d;
    font-size: 12px;
}
.container .info span a {
    color: #000000;
    text-decoration: none;
}
.container .info span .fa {
    color: #ef3b3a;
}
body {
    background: #d1e8ff; /* fallback for old browsers */
    background: rgb(209, 232, 255);
    background: linear-gradient(
            90deg,
            rgba(209, 232, 255, 1) 0%,
            rgba(125, 202, 255, 1) 50%,
            rgba(61, 161, 242, 1) 100%
    );
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

